@use '../abstract/breakpoints' as *;
@use '../abstract/colors' as *;
@use "../abstract/fonts" as *;
@use '../abstract/functions' as *;
@use '../abstract/mixins' as *;


.nav{
    &__menu{
        @include flex-center-vertical;
        @include breakpoint-lg{
            gap:rem(16);
        }
        @include breakpoint-xl{
            gap:rem(32);
        }
    }

    &__link{
        position: relative;
        overflow: hidden;
        display: inline-flex;
        align-items: center;
        font-size: rem(18);
        line-height: 1.85;
        font-family: $ff-tertiary;
        @include breakpoint-xl{
            font-size: rem(20);
        }
        span{
            color:$clr-black;
            display: inline-flex;
            margin: 0;
            transition: transform 0.3s ease;
        }

        &.active,
        &:hover{
            span{
                color:$clr-secondary; 
            }
        }
    }
}


// ===== MObile Menu Start ===========
.mobile-menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100vh;
    background: $clr-primary;
    transform:translateX(-100%);
    transition:transform 0.2s ease-out;
    @include breakpoint-lg{
        transform:translateX(-100%) !important;
    }
    &__inner{
        display: flex;
    }
    &__list{
        li{
            position: relative;
            overflow: hidden;
        }  
    }
    &__link{
        color:$clr-white;
        font-size: rem(32);
        line-height: 2;
        font-family: $ff-secondary;
        font-weight: 700;
        display: inline-flex;
        align-items: center;
        position: relative;
        overflow: hidden;
        padding-right: 14px;
        @include breakpoint-md{
            font-size: rem(36);
        }
        @include breakpoint-lg{
            font-size: rem(40);
        }
        span{
            display: inline-flex;
            margin: 0;
            transition: transform .3s ease;
            &:first-child{
                transform: translate(0px,220%);
            }
        }

        &::after {
            content: attr(data-attr);
            position: absolute;
            height: 100%;
            top: 0;
            left: 32px;
            text-transform: capitalize;
            transform: translate3d(0, 220%, 0);
            transition: transform .3s ease;
            color: $clr-secondary;
        }

        &.active,
        &:hover{
            color:$clr-secondary;
            &::after {
                transform: translate3d(0, 0, 0);
            }
            span{  
                &:first-child{
                    transform: translate(0);
                }
                &:last-child{
                    transform: translate3d(0, -220%, 0);
                }
            }     
        }
    }
}

// ===== MObile Menu End ===========


// ===== Close-Menu-Button Start ===========
.close-menu-btn{
    position: absolute;
    right: 22px;
    top: 22px;
    &:hover{
        cursor: pointer;
        svg path{
            fill:$clr-secondary;
        }
    }
}
// ===== Close-Menu-Button Start ===========


